  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.3px;
}

.logo__item {
  height: 32px;
}

.buttonSlider {
  width: 32px;
  height: 32px;
  background: #fff;
