.menu-link {
    position: relative;
    display: inline-flex;
    border-radius: 36px;
    transition-property: background-color;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.menu-link.current {
    background-color: #EAC645;
    padding: 8px 16px;
    color: #111111;
}

.menu-link.current::after {
    position: absolute;
    width: 100%;
    height: 34px;
    display: block;
    background-color: #EAC645;
    
}

