.mobile-menu {
    
        /* margin-top: 65px; */
        position:fixed;
    z-index: 998;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4F2EE8 18.03%, #FFFFFF 100%);
    pointer-events: none;
    visibility: hidden;
    opacity: 0;
    transform: translateX(100%);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1), opacity 250ms cubic-bezier(0.4, 0, 0.2, 1), visibility 250ms cubic-bezier(0.4, 0, 0.2, 1); 
}

.is-open {
    pointer-events: initial;
    opacity: 1;
    visibility: visible;
    overflow: auto;
    transform: translateX(0);
}
