.shopping-list-section {
    width: 100%;
    height: 100%;
    /* padding-top: 40px; */
}

@media screen and (min-width: 768px) {
    .shopping-list-section {
        height: 935px;
    }
}

@media screen and (min-width: 1440px) {
    .shopping-section {
        display: flex;
        gap: 40px;
    }
}

.shopping-support {
    display: none;
}

@media screen and (min-width: 1440px) {
    .shopping-support {
        display: block;
    }
}

.shopping-part {
    font-weight: 400;
    letter-spacing: -0.04em;
    color: var(--black-color);
}

