.shopping-empty {
    width: 244px;
    margin: 120px auto 302px auto;
    text-align: center;
}

@media screen and (min-width: 768px) {
    .shopping-empty {
        width: 349px;
        margin: 140px auto 394px auto;
    }
}

@media screen and (min-width: 1440px) {
    .shopping-empty {
        margin-left: 355px;
        margin-bottom: 157px;
    }
}

.shopping-text {
    margin-bottom: 14px;
    font-size: 14px;
    line-height: 1.29;
    letter-spacing: -0.02em;
    color: var(--shopping-list-text-color);
}

@media screen and (min-width: 768px) {
    .shopping-text {
        margin-bottom: 20px;
        font-size: 18px;
        line-height: 1.34;
    }
}

.shopping-empty-book {
    margin: 0 auto;
}

.shopping-book-card {
    position: relative;
    width: 295px;
    margin: 0 auto 20px 0;
    display: flex;
    border: 2px solid rgba(79, 46, 232, 0.4);
    border-radius: 16px;
}

@media screen and (min-width: 375px) {
    .shopping-book-card {
        width: 335px;
    }
}

@media screen and (min-width: 768px) {
    .shopping-book-card {
        width: 704px;
  }
}

@media screen and (min-width: 1440px) {
    .shopping-book-card {
        width: 996px;
    }
}

.shopping-card-img {
    margin: 14px;
    border-radius: 8px;
    background-color: rgb(180, 79, 150);
}

@media screen and (min-width: 768px) {
    .shopping-card-img {
        margin: 24px;
        width: 116px;
        height: 165px;
    }
}

.shopping-title-card {
    margin-top: 14px;
    margin-bottom: 4px;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.12;
    text-transform: uppercase;
}

@media only screen and (min-width: 768px) {
    .shopping-title-card {
        margin-top: 24px;
  }
}

.shopping-category-card {
    margin-bottom: 8px;
    font-size: 12px;
    line-height: 1.17;
    letter-spacing: 0.03em;
    color: #B4AFAF;
}

@media screen and (min-width: 768px) {
    .shopping-category-card {
        margin-bottom: 14px;
    }
}

.shopping-desc-card {
    margin-bottom: 25px;
    max-width: 193px;
    font-size: 14px;
    line-height: 1.29;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
}

@media screen and (min-width: 768px) {
    .shopping-desc-card {
        max-width: 508px;
        margin-bottom: 31px;
    }
}

@media screen and (min-width: 1440px) {
    .shopping-desc-card {
        max-width: 804px;
    }
}

.shopping-bottom-card {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
}

.shopping-author-card {
    font-size: 12px;
    font-style: italic;
    line-height: 1.17;
    color: #B4AFAF;
}

.shopping-site {
    width: 80px;
    display: flex;
    align-items: center;
    gap: 8px;
    filter: hue-rotate(180deg) saturate(0%);
}

@media screen and (min-width: 768px) {
    .shopping-site {
        width: 149px;
  }
}

.shopping-close-btn {
    position: absolute;
    top: 14px;
    right: 14px;
    background-color: var(--categories-links-active-color);
    border-radius: 50%;
    width: 28px;
    height: 28px;
}

@media screen and (min-width: 768px) {
    .shopping-close-btn {
        top: 24px;
        right: 24px;
        width: 34px;
        height: 34px;
    }
}

.shopping-icon {
    fill: var(--white-color);
}